<template>
  <transition name="slide">
    <div class="hs-container-child" v-show="show">
      <c-header :title="$t('base.SelectionResult')" leftIcon1="return.png" :leftFn1="close"></c-header>
      <hs-scroll
        class="table-view"
        ref="tableView"
        :data="[details]">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false" v-for="(item, index) in details.ChooseSeats" :key="index">
              <div class="custom-cell-card">
                <div class="card-title">
                  <div class="left">
                    <div class="mark">{{_showSegmentTitle(item.Segment.ID)}}</div>
                    <div class="logo" :style="{'background-image': `url(${item.Segment.AirLineLogoUrl})`}"></div>
                    <p>{{item.Segment.AirLineName}}<template>&nbsp;<span v-if="!noValue(item.Segment.ActualCarryFlightNo)">{{$t('base.CodeShare')}}</span></template>&nbsp;{{item.Segment.FlightNo}}<template v-if="!noValue(item.Segment.Plane)">&nbsp;|&nbsp;{{item.Segment.Plane}}</template><template v-if="!noValue(item.Segment.Meal)">&nbsp;|&nbsp;{{item.Segment.Meal}}</template>
                    </p>
                  </div>
                </div>
                <div class="trip">
                  <div class="item">
                    <p>{{beTime(item.Segment.DepartDate, 'MM-dd')}} {{getWeeks(beTime(item.Segment.DepartDate))}}</p>
                    <p>{{beTime(item.Segment.DepartDate, 'hh:mm')}}</p>
                    <p>{{item.Segment.DepartAirportName}}{{item.Segment.DepartTerminal}}</p>
                  </div>
                  <div class="logo" style="background-image: url('static/image/plane-ticket/online-select-seat/plane.png')"></div>
                  <div class="item">
                    <p>{{beTime(item.Segment.ArrivalDate, 'MM-dd')}} {{getWeeks(beTime(item.Segment.ArrivalDate))}}</p>
                    <p>{{beTime(item.Segment.ArrivalDate, 'hh:mm')}}<span v-if="item.Segment.AddDays > 0">+{{item.Segment.AddDays}}</span></p>
                    <p>{{item.Segment.ArrivalAirportName}}{{item.Segment.ArrivalTerminal}}</p>
                  </div>
                </div>
                <div class="infors">
                  <div class="item">
                    <p>{{$t('base.Name')}}</p>
                    <p>{{details.Name}}</p>
                  </div>
                  <div class="item">
                    <p>{{$t('base.CabinLevel')}}</p>
                    <p>{{item.Segment.Cabin}}</p>
                  </div>
                  <div class="item">
                    <p>{{$t('base.SeatNumber')}}</p>
                    <p>{{item.RowNumber}}{{item.SeatNumber}}</p>
                  </div>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-rules">
                <div class="title">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/online-select-seat/warning.png')"></div>
                  <p>{{$t('base.Precautions')}}</p>
                </div>
                <p>{{$t('base.ImportantThisSelectionIsNotAnOnlineCheckIn')}}</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <!-- <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-selected">以下乘客成功选座</div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-selected-people">
                <div class="logo" style="background-image: url('static/image/base/check-box/check-box.png')"></div>
                <p>张某某</p>
              </div>
            </hs-cell-1>
          </hs-section> -->
          <!-- <div style="width: 100%; padding: 20px 0;">
            <hs-button class="footer-button" width="70%" @click="_share">把座位信息分享给Ta</hs-button>
          </div> -->
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsButton from 'base/hs-button/hs-button'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  data() {
    return {
      details: {},
      Segments: [],
      show: false
    }
  },
  methods: {
    // _share() {
    //   console.log('分享')
    // }
    open(details, Segments) {
      this.details = details
      this.Segments = Segments
      this.details.ChooseSeats = this.details.ChooseSeats.map(v => {
        let Segment = Segments.filter(vc => vc.ID === v.SegmentID)[0]
        v.Segment = Segment
        return v
      })
      console.log('处理过的details: ', this.details)
      this.show = true
    },
    close() {
      this.show = false
    },
    // 根据航段显示单程/往返程/N程
    _showSegmentTitle(ID) {
      let index = this.Segments.map(v => v.ID).indexOf(ID)
      let length = this.Segments && !this.noValue(JSON.stringify(this.Segments)) ? this.Segments.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.Segments[0].DepartCityName === this.Segments[1].ArrivalCityName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    }
  },
  components: {
    CHeader,
    HsButton,
    HsScroll,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  background-color $color_white
  z-index 200
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell-card
      width 100%
      >.card-title
        @extend .flex-row
        @extend .flex-start
        width 100%
        margin-top 15px
        >.left
          @extend .flex-row
          @extend .flex-start
          >.mark
            @extend .flex-center
            font-size 0.7rem
            border-radius 2px
            border 1px solid $color_theme
            color $color_theme
            padding 3px 0
            width 40px
            margin-right 10px
          >.logo
            width 15px
            height 15px
            background-size 100% 100%
            margin-right 10px
          >p
            font-size 0.9rem
        >p
          font-size 0.9rem
          color $color_gray_text
      >.trip
        @extend .flex-row
        @extend .flex-center
        width 100%
        border-bottom 1px dashed $color_gray_layer
        >.item
          @extend .flex-column
          @extend .flex-center
          width calc(50% - 20px)
          padding 20px 0
          >p
            &:nth-child(2)
              font-size 1.2rem
              font-weight bold
              position relative
              >span
                color $color_theme
                font-size 0.8rem
                position absolute
                top -4px
                right -16px
            &:nth-child(1)
            &:nth-child(3)
              font-size 0.9rem
              margin 4px 0
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          transform rotate(90deg)
      >.infors
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        flex-wrap wrap
        width 100%
        padding-bottom 20px
        >.item
          @extend .flex-column
          @extend .flex-center
          width 33.33%
          margin-top 20px
          >p
            &:nth-child(1)
              font-size 0.9rem
              color $color_gray_text
              margin-bottom 13px
            &:nth-child(2)
              font-size 1rem
    .custom-cell-rules
      width 100%
      padding 20px 0
      >.title
        @extend .flex-row
        @extend .flex-start
        width 100%
        margin-bottom 10px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          margin-right 10px
        >p
          font-size 1.1rem
      >p
        font-size 0.8rem
        color $color_gray_text
        line-height 1.5em
    .custom-cell-selected
      @extend .flex-row
      @extend .flex-start
      width 100%
      font-size 1.1rem
    .custom-cell-selected-people
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 10px
      >p
        font-size 0.8rem
    .footer-button
      margin 0 auto
</style>



