<template>
  <div class="hs-container" v-if="!this.noValue(JSON.stringify(details))">
    <c-header :title="$t('base.OrderDetails')" isBackNav></c-header>
    <hs-scroll
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getOrderDetails">
      <ul>
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="state-details">
              <div class="header" :type="language">
                <p>{{details.OrderStatusDesc}}</p>
                <p>{{details.VettingStatusDesc}}</p>
                <p>{{details.OrderStatusDesc}}</p>
              </div>
              <p class="infor">{{$t('base.OrderNumber')}}: {{details.TravelID}}</p>
              <p class="infor">{{$t('base.OrderDate')}}: {{$t(beTime(details.OrderDate), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(details.OrderDate, 'hh:mm')}}</p>
              <div class="buttons" v-if="$route.query.isOwner">
                <!-- <hs-button
                  class="button"
                  v-if="(details.SettlementType === $t('base.SettleNow') && Number(details.PayStatus) === 0) && (Number(details.OrderStatus) === 1 || Number(details.OrderStatus) === 8) && (Number(details.VettingStatus) === 0 || Number(details.VettingStatus) === 5)"
                  @click="Number(details.OrderStatus) === 8 ? _gotoSubmitAndPay() : _gotoPay()">
                  {{Number(details.OrderStatus) === 8 ? $t('base.SubmitAndPay') : $t('base.Pay')}}
                </hs-button>
                <hs-button class="button" @click="_submit" v-else-if="Number(details.OrderStatus) === 8">{{$t('base.Submit')}}</hs-button>
                <hs-button class="button" type="common" @click="_cancel" v-if="Number(details.OrderStatus) === 1 || Number(details.OrderStatus) === 2 || Number(details.OrderStatus) === 8">{{$t('base.CancelOrder')}}</hs-button> -->
                <hs-button v-if="details.PageBtnInfo.IsAllowPayAndSubmit" @click="_gotoSubmitAndPay"
                  class="button">{{$t('base.SubmitAndPay')}}</hs-button>
                <hs-button v-if="details.PageBtnInfo.IsAllowPay" @click="_gotoPay"
                  class="button">{{$t('base.Pay')}}</hs-button>
                <hs-button v-if="details.PageBtnInfo.IsAllowSubmit" @click="_submit"
                  class="button">{{$t('base.Submit')}}</hs-button>
                <hs-button v-if="details.PageBtnInfo.IsAllowCancel" @click="_cancel"
                  class="button"
                  type="common">{{$t('base.CancelOrder')}}</hs-button>
              </div>
              <div class="buttons" v-if="infoIndex >= 0 && $route.query.isExamine && Number(details.VettingStatus) === 1">
                <hs-button class="button" @click="[vettingContent = 1, showAlert = true]">{{$t('base.Pass')}}</hs-button>
                <hs-button class="button" type="common" @click="[vettingContent = 0, showAlert = true]">{{$t('base.Reject')}}</hs-button>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <hs-section v-for="(item, index) in details.Segments" :key="index" v-if="details.Segments && !noValue(JSON.stringify(details.Segments))">
          <hs-cell-1 :showLogo="false">
            <div class="travel-details">
              <div class="time">
                <div class="mark" :type="language">{{_showSegmentTitle(index)}}</div>
                <p>{{$t(beTime(item.DepartDate), 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(beTime(item.DepartDate))}} {{item.DepartCityName}}&nbsp;-&nbsp;{{item.ArrivalCityName}}</p>
              </div>
              <div class="plane">
                <div class="logo" :style="{'background-image': `url(${item.AirLineLogoUrl})`}"></div>
                <div class="details">
                  <p>
                    {{item.AirLineName}}
                    <template><span v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.CodeShare')}}</span></template>
                    {{item.FlightNo}}
                    <template v-if="!noValue(item.Plane)">|&nbsp;{{item.Plane}}</template>
                    <template v-if="!noValue(item.Meal)">|&nbsp;{{item.Meal}}</template>
                    <template v-if="!noValue(item.Cabin)">|&nbsp;{{item.Cabin}}</template>
                  </p>
                  <p v-if="!noValue(item.ActualCarryFlightNo)">{{$t('base.OperatedBy')}}&nbsp;{{item.ActualCarryFlightNo}}</p>
                </div>
              </div>
              <div class="details">
                <div class="source">
                  <p>{{beTime(item.DepartDate, 'hh:mm')}}</p>
                  <p>{{item.DepartAirportName}} {{item.DepartTerminal}}</p>
                </div>
                <div class="to">
                  <div class="stopOver-logo" v-if="item.StopOver">
                    <p>{{$t('base.By')}}</p>
                  </div>
                  <div class="logo" v-else></div>
                  <div class="dashed"></div>
                  <p class="stopOver" v-if="item.StopOver">{{item.StopOver.StopCity}}</p>
                </div>
                <div class="destination">
                  <p>
                    {{beTime(item.ArrivalDate, 'hh:mm')}}
                    <span v-if="item.AddDays > 0">
                      <template v-if="language === 'CN'">+{{item.AddDays}}天</template>
                      <template v-else>+{{item.AddDays}}</template>
                    </span>
                  </p>
                  <p>{{item.ArrivalAirportName}} {{item.ArrivalTerminal}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false" :showSeparator="!item.openLowestPrice" v-if="item.LowestPrice" @click.native="item.openLowestPrice = !item.openLowestPrice">
            <div class="min-price-details-header">
              <p>{{$t('base.LowestPriceDetails')}}</p>
              <div style="background-image: url('static/image/base/cell/right_arrow_gray.png')" :style="{transform: `rotate(${item.openLowestPrice ? '90deg' : '-90deg'})`}"></div>
            </div>
          </hs-cell-1>
          <div class="min-price-details" v-if="item.LowestPrice && item.openLowestPrice">
            <div>
              <p>{{$t('base.XHoursBeforeAndAfterTheLowestPrice', 'function', item.LowestPrice.FloorTimeRange)}}</p>
              <p class="price">￥{{item.LowestPrice.LowestPrice}}</p>
            </div>
            <div>
              <p>{{$t('base.CheapestFlight')}}</p>
              <p>{{item.LowestPrice.FlightNo}}</p>
            </div>
            <div>
              <p>{{$t('base.ReasonForNotSelectingTheCheapestFlight')}}</p>
              <p>{{item.LowestPrice.UnChoosedReason}}</p>
            </div>
          </div>
          <hs-cell-1 :showLogo="false">
            <div class="cost-details" :type="language">
              <div>
                <div v-if="item.CanChange" @click="_gotoReschedule(item.PnrID)">{{$t('base.Rescheduled')}}</div>
                <div v-if="item.CanRefund" @click="_gotoRefund(item.PnrID)">{{$t('base.Refund')}}</div>
                <div v-if="item.CanChooseSeat" @click="$refs.onlineSelectSeat.open(details.ID, item.ID)">{{$t('base.Selection')}}</div>
              </div>
              <p @click="_getFlightCabinRule(item.Rule)">{{$t('base.TicketRestrictions')}} ></p>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 乘客信息 -->
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in details.Passengers" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.Passenger')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === details.Passengers.length - 1 ? '0px' : '1px'}">
                <div class="name">
                  <p>{{item.Name}}</p>
                  <span v-if="!noValue(JSON.stringify(item.ChooseSeats))" @click="$refs.onlineSelectSeatResult.open(item, details.Segments)">{{$t('base.SeatInfo')}} ></span>
                </div>
                <div class="others">
                  <p>{{item.Credential.CredentialName}} {{item.Credential.CredentialNo}}</p>
                  <p v-for="(itemChild, indexChild) in item.Insurances" :key="indexChild" v-if="itemChild.Count > 0"
                    class="insure">
                    {{itemChild.InsuranceCategory}}
                    <template v-if="details.Segments && details.Segments.length > 1">({{itemChild.Order === 1 ? $t('base.Going') : $t('base.Return')}})</template>
                    x {{itemChild.Count}}{{$t('base.Copy')}}
                  </p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>

        <!-- 其他信息 -->
        <hs-section>
          <hs-cell-small-title v-if="details.CustomItem && details.CustomItem.Name"
            :title="details.CustomItemName"
            type="bold"
            :value="details.CustomItem.Name"
            :showLogo="false"
          ></hs-cell-small-title>
          <hs-cell-small-title v-if="details.Purpose"
            :title="$t('base.TravelPurpose')"
            type="bold"
            :value="details.Purpose"
            :showLogo="false"
          ></hs-cell-small-title>
          <hs-cell-small-title v-if="details.AuthorizationCode"
            :title="$t('base.AuthorizationCode')"
            type="bold"
            v-model="details.AuthorizationCode"
            :showLogo="false"
          ></hs-cell-small-title>
          <hs-cell-small-title
            :title="$t('base.BookingSource')"
            type="bold"
            :value="details.BookingSourceDesc"
            :showLogo="false"
          ></hs-cell-small-title>
          <template v-if="language === 'CN'">
            <hs-cell-small-title v-if="details.VettingRecordInfos && details.VettingRecordInfos.length > 0" @click.native="showExamineProcess = true"
              :title="$t('base.ApprovalInformation')"
              type="bold"
            ></hs-cell-small-title>
          </template>
          <template v-else>
            <hs-cell-small-title v-if="details.VettingRecordInfos && details.VettingRecordInfos.length > 0" @click.native="showExamineProcess = true"
              :title="$t('base.ApprovalInformation')"
              type="bold"
              :value="details.VettingStatusDesc"
            ></hs-cell-small-title>
          </template>
          <hs-cell-small-title v-if="Number(details.TravelType) === 0"
            :title="$t('base.PaymentWay')"
            type="bold"
            :value="details.PayType | filterPayType"
            :showLogo="false"
          ></hs-cell-small-title>
          <hs-cell-small-title v-if="details.ReasonCode"
            :title="$t('base.ViolationOfRank')"
            type="bold"
            :value="details.ReasonCode"
            :warning="false"
            :showLogo="false"
            @header="_getTravelStandard"
          ></hs-cell-small-title>
        </hs-section>

        <!-- 联系人 -->
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in details.Contacts" :key="index">
            <div class="custom-cell-people">
              <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
                <p>{{$t('base.ContactInformation')}}</p>
              </div>
              <div class="right" :style="{'border-bottom-width': index === details.Contacts.length - 1 ? '0px' : '1px'}">
                <div class="name">
                  <p>{{item.Name}}</p>
                </div>
                <div class="others">
                  <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
      </ul>
    </hs-scroll>
    <div class="footer" @click="_switchDetailsDialog">
      <div class="price">
        <p>{{$t('base.TotalAmount')}}</p>
        <p v-if="!(details.OriginBookingID && details.OrderStatus !== 5)">￥{{details.TotalPrice}}</p>
      </div>
      <div class="details">
        <div class="logo" :style="{transform: showDetailsDialog ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
    </div>
    <hs-dialog v-model="showDetailsDialog" height="calc(100% - 50px)" ref="detailsDialog">
      <hs-scroll class="detailsDialog" :data="[details]" @click.native.stop>
        <ul>
          <hs-section v-if="details.FeeDetails[0].ChargeInfo">
            <hs-cell-1 :showLogo="false" v-if="details.FeeDetails[0].ChargeInfo.SalePrice >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.AirTickets')}}</p>
                <p><span>￥{{details.FeeDetails[0].ChargeInfo.SalePrice}}</span><span>x {{details.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.FeeDetails[0].ChargeInfo.FuelOilTax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.FuelFee')}}</p>
                <p><span>￥{{details.FeeDetails[0].ChargeInfo.FuelOilTax}}</span><span>x {{details.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.FeeDetails[0].ChargeInfo.AirportTax >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.PlaneConstructionFee')}}</p>
                <p><span>￥{{details.FeeDetails[0].ChargeInfo.AirportTax}}</span><span>x {{details.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.FeeDetails[0].ChargeInfo.ServiceCharge >= 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.ServiceFee')}}</p>
                <p><span>￥{{details.FeeDetails[0].ChargeInfo.ServiceCharge}}</span><span>x {{details.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" v-if="details.FeeDetails[0].ChargeInfo.Rebate > 0">
              <div class="detailsDialog-custom-cell">
                <p>{{$t('base.FrontReturn')}}</p>
                <p><span>-￥{{details.FeeDetails[0].ChargeInfo.Rebate}}</span><span>x {{details.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
              </div>
            </hs-cell-1>
            <hs-cell-1 v-for="(item, index) in insurances" :key="index" :showLogo="false">
              <div class="detailsDialog-custom-cell">
                <p>{{item.InsuranceCategory}}</p>
                <p><span>￥{{item.SalePrice}}</span><span>x {{item.number}}{{$t('base.Copy')}}</span></p>
              </div>
            </hs-cell-1>
          </hs-section>
          <div class="detailsDialog-tips" v-if="details.OriginBookingID && details.OrderStatus !== 5">
            <p>{{$t('base.TheReschedulingFeeIsSubjectToTheReviewOfTheAirline')}}</p>
          </div>
        </ul>
      </hs-scroll>
    </hs-dialog>
    <c-return-explain v-model="showExplain" :details="returnExplainDetails"></c-return-explain>
    <c-examine-process v-model="showExamineProcess" :businessName="$t('base.Passenger')" :datalist="details.VettingRecordInfos" :passengers="details.Passengers"></c-examine-process>
    <hs-alert :show.sync="showAlert" :title="vettingContent === 1 ? $t('base.ApprovedPass') : $t('base.ApprovalRejection')" cancelBtn="" @confirm="_examineSubmit" maskClose>
      <div class="hs-alert-content" slot="content">
        <input maxlength='50' v-model="vettingMemo" :placeholder="$t('base.PleaseEnterAComment')"/>
      </div>
    </hs-alert>
    <online-select-seat ref="onlineSelectSeat" :details="details" @delegate="_getOrderDetails"></online-select-seat>
    <c-travel-standard v-model="showTravelStandard" :details="flightRight.FlightRank" :travelPolicy="flightRight.TravelPolicy"></c-travel-standard>
    <online-select-seat-result ref="onlineSelectSeatResult"></online-select-seat-result>
    <pay ref="Pay"></pay>
  </div>
</template>
<script type="text/ecmascript-6">
// 机票订单详情
import CHeader from 'components/c-header/c-header'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import CReturnExplain from 'components/c-return-explain/c-return-explain'
import CExamineProcess from 'components/c-examine-process/c-examine-process'
import HsButton from 'base/hs-button/hs-button'
import eventBus from 'common/js/eventBus.js'
import HsAlert from 'base/hs-alert/hs-alert'
import OnlineSelectSeat from '../online-select-seat/online-select-seat'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import OnlineSelectSeatResult from '../online-select-seat/online-select-seat-result'
import Pay from 'pages/pay/pay'
import Vue from 'vue'
import {kpiFlightConfirmOrder, kpiFlightgetOrderDetail, kpiFlightCancelOrder, kpiVettingVettingHandleOrder} from 'api'
import { mapGetters, mapActions } from 'vuex'
export default {
  beforeRouteLeave (to, from, next) {
    if (
      this.showExamineProcess === true ||
      (this.$refs.onlineSelectSeat && this.$refs.onlineSelectSeat.show === true) ||
      (this.$refs.onlineSelectSeatResult && this.$refs.onlineSelectSeatResult.show === true)
    ) {
      this.showExamineProcess = false
      this.$refs.onlineSelectSeat && this.$refs.onlineSelectSeat.close()
      this.$refs.onlineSelectSeatResult && this.$refs.onlineSelectSeatResult.close()
      next(false)
    } else {
      this.showAlert = false
      next()
    }
  },
  created() {
    if (this.noValue(this.$route.query.Id)) {
      this.$router.replace('/planeTicket/orderList')
    } else if (!this.noValue(this.$route.query.Id)) {
      this._getOrderDetails()
    }
  },
  computed: {
    ...mapGetters([
      'isMiniprogram',
      'flightRight'
    ]),
    insurances() {
      let res = []
      this.details.Passengers.forEach(element => {
        if (element.Insurances) {
          let Insurances = element.Insurances.map(v => {
            v.TmpID = `${v.InsuranceCategory}${v.SalePrice}`
            return v
          })
          Insurances.forEach(elementChild => {
            if (elementChild.SalePrice >= 0 && elementChild.Count > 0) {
              let index = res.map(v => v.TmpID).indexOf(elementChild.TmpID)
              if (index < 0) {
                res.push(Object.assign(elementChild, {number: elementChild.Count}))
              } else {
                res[index].number += elementChild.Count
              }
            }
          })
        }
      })
      return res
    },
    infoIndex() {
      return this.details.VettingRecordInfos ? this.details.VettingRecordInfos.map(v => v.TemplateNodeProcessStatusDesc).indexOf('进行中') : -1
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      showDetailsDialog: false,
      showExplain: false,
      returnExplainDetails: {},
      showExamineProcess: false,
      // showExamineDialog: false,
      showAlert: false,
      vettingContent: 1,
      vettingMemo: '',
      showTravelStandard: false
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  methods: {
    ...mapActions([
      'dispatchFlightRight'
    ]),
    // 退改签信息
    _getFlightCabinRule(rule) {
      this.showExplain = true
      this.returnExplainDetails = {
        vhtml: rule
      }
    },
    // 订单详情
    _getOrderDetails() {
      let params = {
        ID: this.$route.query.Id,
        Type: this.$route.query.isOwner ? 0 : 1
      }
      kpiFlightgetOrderDetail(params).then((res) => {
        console.log('订单详情: ', res)
        this.details = res.ResultData
        this.details.Segments.forEach((item, index) => {
          this.$set(this.details.Segments[index], 'openLowestPrice', false)
        })
      })
    },
    // 根据航段显示单程/往返程/N程
    _showSegmentTitle(index) {
      let length = this.details.Segments && !this.noValue(JSON.stringify(this.details.Segments)) ? this.details.Segments.length : 0
      if (length === 1) {
        return this.$t('base.OneWay')
      } else if (length === 2) {
        if (this.details.Segments[0].DepartCityName === this.details.Segments[1].ArrivalCityName) {
          return index === 0 ? this.$t('base.Going') : this.$t('base.Return')
        } else {
          return this.$t('base.NoXSegment', 'function', index + 1)
        }
      } else {
        return this.$t('base.NoXSegment', 'function', index + 1)
      }
    },
    _switchDetailsDialog() {
      if (this.showDetailsDialog) {
        this.$refs.detailsDialog.showDetails = false
      } else {
        this.showDetailsDialog = true
      }
    },
    /** 提交 */
    _submit() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitYourOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            TravelID: this.$route.query.Id
          }
          kpiFlightConfirmOrder(params).then((res) => {
            if (res.ResultData === true) {
              this.$toast(this.$t('TM.SubmitSucceed'))
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({name: 'planeTicketOrderList', params: {TravelType: Number(this.details.TravelType)}})
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    /** 提交并支付 */
    _gotoSubmitAndPay() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitAndPayForTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            TravelID: this.$route.query.Id
          }
          kpiFlightConfirmOrder(params).then((res) => {
            console.log('提交并支付: ', res)
            if (res.ResultData === true) {
              this._getOrderDetails()
              this._gotoPay()
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    // 取消订单
    _cancel() {
      this.$alert({
        content: this.$t('TM.AreYouSureToCancelTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            TravelID: this.$route.query.Id
          }
          kpiFlightCancelOrder(params).then((res) => {
            if (res.ResultData === true) {
              this.$toast(this.$t('TM.OrderCancelled'))
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({name: 'planeTicketOrderList', params: {TravelType: Number(this.details.TravelType)}})
            } else {
              this.$toast(this.$t('TM.OrderCancelFailed'))
            }
          })
        }
      })
    },
    // 审批
    _examineSubmit() {
      let params = {
        ID: this.$route.query.Id,
        InstanceID: this.details.VettingRecordInfos[this.infoIndex].InstanceID,
        TemplateNodeID: this.details.VettingRecordInfos[this.infoIndex].TemplateNodeID,
        VettingContent: this.vettingContent,
        VettingMemo: this.vettingMemo,
        BusinessType: 2 // 国内机票，暂时是写死的
      }
      kpiVettingVettingHandleOrder(params).then((res) => {
        console.log('审批: ', res)
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.ApprovalSubmitted'), {
            complete: () => {
              setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
              this.$router.replace({path: '/message/examine', query: {QuerySource: 0}})
            }
          })
        }
      })
    },
    // 支付
    _gotoPay() {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 1,
        OrderID: this.$route.query.Id,
        PayWay: 3,
        TravelType: Number(this.details.TravelType),
        CallbackPathName: 'planeTicketOrderList'
      })
    },
    // 去退票
    _gotoRefund(PnrID) {
      this.$router.push({
        path: '/planeTicket/toRefund',
        query: {
          ItktID: this.$route.query.Id,
          PnrID: PnrID
        }
      })
    },
    // 去改期
    _gotoReschedule(PnrID) {
      if (Number(this.details.PayType) === 2) {
        this.dialog = this.$createDialog({
          type: 'alert',
          icon: 'cubeic-alert',
          title: this.$t('base.Tips'),
          content: this.$t('base.TheOrderIsForIndividualPaymentOrders')
        })
        this.dialog.show()
        return
      }
      this.$router.push({
        name: 'planeTicketToReschedule',
        query: {
          ItktID: this.$route.query.Id,
          PnrID: PnrID,
          TravelType: Number(this.details.TravelType)
        }
      })
    },
    // 获取差旅职级
    _getTravelStandard() {
      this.dispatchFlightRight().then((res) => {
        this.showTravelStandard = true
      })
    }
  },
  components: {
    CHeader,
    HsDialog,
    HsScroll,
    HsSection,
    HsCell1,
    HsCellSmallTitle,
    CReturnExplain,
    CExamineProcess,
    HsButton,
    HsAlert,
    OnlineSelectSeat,
    CTravelStandard,
    OnlineSelectSeatResult,
    Pay
  }
}
</script>
