<template>
  <transition name="slide">
    <div class="hs-container-child" v-show="show">
      <c-header title="在线选座" leftIcon1="return.png" :leftFn1="close"></c-header>
      <div class="float-top" v-if="planeMap && planeMap.SeatMap">
        <div class="item">
          <div class="logo" style="background-image: url('static/image/plane-ticket/online-select-seat/flight.png')"></div>
          <p>{{planeMap.SeatMap.FlightNo}}</p>
        </div>
        <div class="item">
          <div class="logo" style="background-image: url('static/image/plane-ticket/online-select-seat/seatQuantity.png')"></div>
          <p>{{seatSum}}</p>
        </div>
        <div class="item">
          <div class="logo" style="background-image: url('static/image/plane-ticket/online-select-seat/aircraft.png')"></div>
          <p>{{planeMap.SeatMap.PlaneType}}{{planeMap.SeatMap.CabinCode}}</p>
        </div>
      </div>
      <hs-scroll
        class="table-view"
        ref="tableView"
        :data="[planeMap]">
        <ul v-if="planeMap.SeatMap && planeMap.SeatMap.AirSeatRows && planeMap.SeatMap.AirSeatRows[0]">
          <img class="plane-image" src="static/image/plane-ticket/online-select-seat/plane_top.png">
          <div id="cabin" class="cabin" style="background-image: url('static/image/plane-ticket/online-select-seat/plane_middle.png')">
            <div class="prompt">
              <div class="item">
                <div style="background-image: url('static/image/plane-ticket/online-select-seat/chooseSeat.png')"></div>
                <p>{{$t('base.Optional')}}</p>
              </div>
              <div class="item">
                <div style="background-image: url('static/image/plane-ticket/online-select-seat/selectSeat.png')"></div>
                <p>{{$t('base.Selected')}}</p>
              </div>
              <div class="item">
                <div style="background-image: url('static/image/plane-ticket/online-select-seat/ordinarySeat.png')"></div>
                <p>{{$t('base.Occupied')}}</p>
              </div>
              <div class="item">
                <div style="background-image: url('static/image/plane-ticket/online-select-seat/exit.png')"></div>
                <p>{{$t('base.Exit')}}</p>
              </div>
            </div>
            <div class="item-row" style="margin-bottom: 3px">
              <div class="item"></div>
              <div class="item" v-for="(item, index) in planeMap.SeatMap.AirSeatRows[0].SeatDetails" :key="index">
                <p class="rowNumber">{{item.SeatNumber}}</p>
              </div>
              <div class="item" v-if="planeMap.SeatMap.AirSeatRows[0] && planeMap.SeatMap.AirSeatRows[0].SeatDetails.length < 9"></div>
            </div>
            <div class="item-row" v-for="(item, index) in planeMap.SeatMap.AirSeatRows" :key="index">
              <div class="item"
                :style="{
                width: `calc(${100 / (item.SeatDetails.length + 1)}vw - ${50 / (item.SeatDetails.length + 1)}px)`,
                height: `calc(${100 / (item.SeatDetails.length + 1)}vw - ${50 / (item.SeatDetails.length + 1)}px)`
              }">
                <p class="rowNumber">{{item.RowNumber}}</p>
              </div>
              <div
                class="item"
                :style="{
                  width: `calc(${100 / (item.SeatDetails.length + 1)}vw - ${50 / (item.SeatDetails.length + 1)}px)`,
                  height: `calc(${100 / (item.SeatDetails.length + 1)}vw - ${50 / (item.SeatDetails.length + 1)}px)`
                }"
                @click="_select(item, itemChild)"
                v-for="(itemChild, indexChild) in item.SeatDetails" :key="indexChild">
                <div v-show="_setSeatBackgroundImage(item, itemChild) === 'chooseSeat'" class="backImg" style="background-image: url('static/image/plane-ticket/online-select-seat/chooseSeat.png')"></div>
                <div v-show="_setSeatBackgroundImage(item, itemChild) === 'selectSeat'" class="backImg" style="background-image: url('static/image/plane-ticket/online-select-seat/selectSeat.png')"></div>
                <div v-show="_setSeatBackgroundImage(item, itemChild) === 'ordinarySeat'" class="backImg" style="background-image: url('static/image/plane-ticket/online-select-seat/ordinarySeat.png')"></div>
                <div v-show="_setSeatBackgroundImage(item, itemChild) === 'exit'" class="backImg" style="background-image: url('static/image/plane-ticket/online-select-seat/exit.png')"></div>
              </div>
              <div class="item"
                v-if="planeMap.SeatMap.AirSeatRows[0] && planeMap.SeatMap.AirSeatRows[0].SeatDetails.length < 9"
                :style="{
                width: `calc(${100 / (item.SeatDetails.length + 1)}vw - ${50 / (item.SeatDetails.length + 1)}px)`,
                height: `calc(${100 / (item.SeatDetails.length + 1)}vw - ${50 / (item.SeatDetails.length + 1)}px)`
              }">
                <p class="rowNumber">{{item.RowNumber}}</p>
              </div>
            </div>
          </div>
          <img class="plane-image" src="static/image/plane-ticket/online-select-seat/plane_down.png">
        </ul>
      </hs-scroll>
      <div class="footer" v-if="planeMap.Passengers && ChoosingIndex >= 0">
        <hs-cell-1 :showLogo="false">
          <div class="custom-cell-item">
            <p>{{planeMap.Passengers[ChoosingIndex].Name}}</p>
            <div class="seat">
              <div class="logo" style="background-image: url('static/image/plane-ticket/online-select-seat/seat.png')"></div>
              <p>{{_getChoosingSeat(ChoosingIndex)}}</p>
              <div v-if="_getChoosingSeat(ChoosingIndex) !== $t('base.Unselected')" class="delete" style="background-image: url('static/image/plane-ticket/online-select-seat/delete.png')" @click="_delete"></div>
            </div>
          </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false">
          <div class="custom-cell-bar">
            <div class="list">
              <div
                :class="[{active: index === ChoosingIndex}, {between: index % 3 === 2}]"
                v-for="(item, index) in planeMap.Passengers" :key="index"
                @click="ChoosingIndex = index">
                <p>{{item.Name}}</p>
                <p>{{_getChoosingSeat(index)}}</p>
              </div>
            </div>
            <hs-button width="50%" @click="_gotoSubmit">{{$t('base.Finish')}}</hs-button>
          </div>
        </hs-cell-1>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 机票在线选座
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsButton from 'base/hs-button/hs-button'
import {kpiFlightGetChooseSeatMapInitInfo, kpiFlightSubmitChooseSeat} from 'api'
export default {
  props: {
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      show: false,
      planeMap: {},
      OrderID: '',
      SegmentID: '',
      ChooseSeats: [],
      ChoosingIndex: undefined
    }
  },
  computed: {
    // 座位数
    seatSum() {
      let res = 0
      this.planeMap.SeatMap.AirSeatRows.forEach(element => {
        element.SeatDetails.forEach(elementChild => {
          if (elementChild.SeatStatus !== ' ' && elementChild.SeatStatus !== 'E' && elementChild.SeatStatus !== '=' && elementChild.SeatStatus !== 'I') {
            res++
          }
        })
      })
      console.log('总座位数: ', res)
      return res
    }
  },
  methods: {
    close() {
      this.show = false
    },
    open(OrderID, SegmentID) {
      this.ChooseSeats = []
      this.OrderID = OrderID
      this.SegmentID = SegmentID
      this._getSeatMap()
    },
    _setSeatBackgroundImage(item, itemChild) {
      for (let seat of this.ChooseSeats) {
        if (item.RowNumber === seat.RowNumber && itemChild.SeatNumber === seat.SeatNumber) { // 这个位子已经被选择
          return 'selectSeat'
        }
      }
      switch (itemChild.SeatStatus) {
        case '=': // 过道
          return ''
        case 'E': // 安全出口
          return 'exit'
        case ' ': // 没有座位
          return ''
        case 'I': // 婴儿行(当出口)
          return 'exit'
        case '*': // 可选
          return 'chooseSeat'
        default:
          return 'ordinarySeat'
      }
    },
    _getSeatMap() {
      let params = {
        OrderID: this.OrderID,
        SegmentID: this.SegmentID
      }
      kpiFlightGetChooseSeatMapInitInfo(params).then((res) => {
        console.log('获取在线选座初始信息: ', res)
        this.planeMap = res.ResultData
        this.show = true
        this.planeMap.Passengers = this.planeMap.Passengers.filter(v => v.IsChoosedSeat === false && v.PassengerType !== 3) // 婴儿不能选座
        this.ChoosingIndex = 0
      })
    },
    _getChoosingSeat(ChoosingIndex) {
      if (ChoosingIndex >= 0 && this.planeMap.Passengers[ChoosingIndex]) {
        let index = this.ChooseSeats.map(v => v.PassengerID).indexOf(this.planeMap.Passengers[ChoosingIndex].ID)
        if (index >= 0) {
          return `${this.ChooseSeats[index].RowNumber}${this.ChooseSeats[index].SeatNumber}`
        } else {
          return this.$t('base.Unselected')
        }
      }
    },
    _select(item, itemChild) {
      if (itemChild.SeatStatus !== '*') { // 该座位不可选
        return
      }
      for (let index in this.ChooseSeats) {
        if (item.RowNumber === this.ChooseSeats[index].RowNumber && itemChild.SeatNumber === this.ChooseSeats[index].SeatNumber) { // 该座位已经被选择
          let indexInPassengers = this.planeMap.Passengers.map(v => v.ID).indexOf(this.ChooseSeats[index].PassengerID)
          this.planeMap.Passengers[indexInPassengers].IsChoosedSeat = false
          this.ChooseSeats.splice(index, 1)
          return
        }
      }
      this.ChooseSeats.unshift({
        SegmentID: this.SegmentID,
        PassengerID: this.planeMap.Passengers[this.ChoosingIndex].ID,
        RowNumber: item.RowNumber,
        SeatNumber: itemChild.SeatNumber,
        SeatPosition: itemChild.SeatPosition,
        Name: this.planeMap.Passengers[this.ChoosingIndex].Name
      })
      this.ChoosingIndex = (this.ChoosingIndex + 1) % this.planeMap.Passengers.length
      // 去重
      let maplist = new Map()
      this.ChooseSeats = this.ChooseSeats.filter(a => !maplist.has(a.PassengerID) && maplist.set(a.PassengerID, 1))
    },
    _delete() {
      let index = this.ChooseSeats.map(v => v.PassengerID).indexOf(this.planeMap.Passengers[this.ChoosingIndex].ID)
      if (index >= 0) {
        this.ChooseSeats.splice(index, 1)
      }
    },
    // 提交选择的座位
    _gotoSubmit() {
      let params = {
        OrderID: this.OrderID,
        SegmentID: this.SegmentID,
        ChooseSeats: this.ChooseSeats
      }
      if (this.noValue(JSON.stringify(this.ChooseSeats))) {
        this.$toast(this.$t('TM.PleaseChooseASeat'))
        return
      }
      kpiFlightSubmitChooseSeat(params).then((res) => {
        console.log('提交选座信息: ', res)
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.SeatSelectionSucceed'))
          this.close()
          this.$emit('delegate')
        } else {
          this.$toast(this.$t('TM.TheSystemIsBusyPleaseTryAgainLater'))
        }
      })
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsCell1,
    HsButton
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
@import '~common/stylus/mixin'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  background-color $color_white
  z-index 200
  >.float-top
    @extend .flex-row
    @extend .flex-center
    position absolute
    left 0
    top 45px
    width 100%
    height 50px
    background-color $color-black_1
    z-index 9
    >.item
      @extend .flex-row
      @extend .flex-center
      width 33.33%
      height 100%
      color $color_white
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
      >p
        font-size 1rem
        margin-left 5px
  >.table-view
    width 100%
    height calc(100% - 44px)
    overflow hidden
    .plane-image
      width 100%
      margin 0 auto
    .cabin
      @extend .flex-column
      @extend .flex-center
      width 100%
      min-height 300px
      position relative
      background-size 100% auto
      background-position center top
      padding 0 25px
      >.prompt
        @extend .flex-row
        @extend .flex-center
        width 100%
        padding 0 25px
        margin-bottom 30px
        >.item
          @extend .flex-column
          @extend .flex-center
          flex 1
          >div
            width 20px
            height 20px
            background-size 100% 100%
            margin-bottom 5px
          >p
            font-size 0.7rem
            color $color_black_text_1
      >.item-row
        @extend .flex-row
        @extend .flex-center
        width 100%
        color $color_black_text_1
        font-size 1rem
        margin 2px 0
        >.item
          @extend .flex-center
          flex 1
          max-width 30px
          max-height 30px
          position relative
          >.backImg
            position absolute
            top 0
            left 0
            width 100%
            height 100%
            background-size calc(100% - 2px) calc(100% - 2px)
            background-position center center
            background-repeat no-repeat
          >.rowNumber
            font-size 0.7rem
            $color_black_text_1
  >.footer
    position absolute
    bottom 0
    left 0
    width 100%
    min-height 44px
    background-color $color_white
    box-shadow: 0 -1px 2px $color_gray_layer
    .custom-cell-bar
      @extend .flex-column
      @extend .flex-start
      width 100%
      padding 10px 0
      >.list
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        flex-wrap wrap
        width 100%
        padding 5px 0
        >div
          @extend .flex-column
          @extend .flex-center
          color $color_theme
          border 1px solid $color_theme
          padding 4px 8px
          width calc(33.33% - 10px)
          margin-right 15px
          margin-bottom 10px
          border-radius 3px
          >p
            ellipsis(1)
            font-size 0.8rem
            &:nth-child(1)
              margin-bottom 2px
              height 1em
        >.active
          color $color_white
          background-color $color_theme
        >.between
          margin-right 0 !important
    .custom-cell-item
      @extend .flex-row
      @extend .flex-between
      width 100%
      >.seat
        @extend .flex-row
        @extend .flex-start
        >.logo
          width 16px
          height 20.5px
          background-size 100% 100%
          margin-right 10px
        >p
          font-size 1rem
          color $color_theme
        >.delete
          width 20px
          height 20px
          background-size 100% 100%
          margin-left 15px
      >p
        font-size 1rem
        font-weight bold
</style>



